<template>
  <v-content>
    <v-layout row wrap>
      <v-flex class="primary">
        <h1 class="text-xs-center white--text">Testing WebRTC Media</h1>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="mt-4">
      <v-flex xs5 offset-xs1>
        <v-card
          :min-height="height"
          :min-width="width"
        >
          <v-card-title primary-title class="primary white--text">
            <h3>Smile you are on Camera</h3>
          </v-card-title>
          <v-card-text>
              <video id="video" v-on:canplay="playStreaming()" class="black"></video>
          </v-card-text>
          <v-card-actions>
            <!-- <v-btn @click="initCamera()">Start Camera</v-btn>
            <v-btn @click="stopCamera()">Stop Camera</v-btn> -->
            <v-btn @click="takePic()">Take picture</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
      <v-flex xs5  class="ml-4">
        <v-card>
          <v-card-title primary-title class="primary white--text">
            <h3>How Do You Look?</h3>
          </v-card-title>
          <v-card-text>
             <canvas id="canvas" style="display: none"></canvas>
             <v-img
              :src="imageURL"
              :height="height"
              :width="width"
              position="center"
            ></v-img>
          </v-card-text>
          <v-card-actions>
            <v-btn @click="clear()">Clear</v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-content>
</template>
<script>
export default {
  props: {
    msg: String
  },
  data () {
    return {
      height: 0,
      filter: 'none',
      width: 500,
      streaming: false,
      video: '',
      canvas: '',
      imageURL: 'https://via.placeholder.com/150?text=No+Image'
    }
  },
  mounted () {
    this.$nextTick(() => {
      this.video = document.getElementById('video')
      this.canvas = document.getElementById('canvas')
      window.navigator.mediaDevices.getUserMedia({ video: true, audio: false })
        .then(stream => {
          this.video.srcObject = stream
          this.video.play()
        })
        .catch(err => {
          console.log(err)
        })
    })
  },
  methods: {
    // initCamera () {
    //   this.video = document.getElementById('video')
    //   this.canvas = document.getElementById('canvas')
    //   navigator.mediaDevices.getUserMedia({ video: true, audio: false })
    //     .then(stream => {
    //       console.log(this.video)
    //       this.video.srcObject = stream
    //       console.log(this.video.srcObject)
    //       this.video.play()
    //     })
    //     .catch(err => {
    //       console.log(err)
    //     })
    // },
    playStreaming () {
      if (!this.streaming) {
        // Set video / canvas height
        this.height = this.video.videoHeight / (this.video.videoWidth / this.width)
        this.video.setAttribute('width', this.width)
        this.video.setAttribute('height', this.height)
        this.streaming = true
      }
    },
    takePic () {
      let context = this.canvas.getContext('2d')
      if (this.width && this.height) {
        this.canvas.width = this.width
        this.canvas.height = this.height
        context.drawImage(this.video, 0, 0, this.width, this.height)
        this.imageURL = this.canvas.toDataURL('image/png')
      }
    },
    clear () {
      this.imageURL = 'https://via.placeholder.com/150?text=No+Image'
    }
  }
}
</script>

